.blogs-title-container {
  border-top: 2px solid #ff7c36;
  border-bottom: 1px solid #ff7c36;
}

.blogs-title-text {
  color: #ff7c36;
}

.blog-item-container {
  cursor: pointer;
  border-bottom: 1px solid #ced4da;
}

.blog-item-container .card{
  border: none;
}

.blog-item-image-container {
  max-height: 400px;
  background-size: cover;
  background-position: bottom;
  background-repeat: no-repeat;
}

.blog-item-image-container img {
  opacity: 0;
}

.like-btn {
  border-color: #ff7c36;
  background: #ff7c36;
}

.like-btn:hover {
  border-color: #dc5f1d;
  background: #dc5f1d;
}

.like-btn:not(:disabled):not(.disabled):active {
  border-color: #dc5f1d;
  background: #dc5f1d;
}

.like-btn.focus, .btn-primary:focus {
  background-color: #dc5f1d;
  border-color: #dc5f1d;
  box-shadow: 0 0 0 0.2rem #dc5f1d;
}